@font-face {
  font-family: "press_start_k";
  src: url("./assets/font/prstartk.eot"),
       url("./assets/font/prstartk.eot?#iefix") format("embedded-opentype"),
       url("./assets/font/prstartk.woff2") format("woff2"),
       url("./assets/font/prstartk.woff") format("woff"),
       url("./assets/font/prstartk.ttf") format("truetype"),
       url("./assets/font/prstartk.svg#press_start_kregular") format("svg");
  font-weight: normal;
  font-style: normal;
}

.Game {
  padding: 0;
  margin: 0;
  overflow: hidden;
  image-rendering: -moz-pixelated; /* Firefox */
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -webkit-optimize-contrast;
      image-rendering: -moz-crisp-edges;
      image-rendering: -o-pixelated;
      image-rendering: pixelated;
  
  position: absolute;
  width: 100%;
  height: 100%;
}

.Ground {
  position: absolute;
  background:
    url("./assets/grass.png") top repeat-x,
    url("./assets/ground.png") top repeat #589C2A;
  width: 100%;
  min-width: 50px;
  height: 40px;
  bottom: 0;
}



